import React from 'react'
import {InfoHome,
        HeroSvg,
        HomeLeft,
        HomeRight,
        ContainerVideo,
        ContainerText,
        ButtomHome,
        ButtonMobile
    } from './InfoHRFHome.elements'
import ReactPlayer from 'react-player'
import {Link} from 'react-router-dom'


const InfoHRFHome = (
    {vector}
) => {
    return (
        <>
        <InfoHome >
            <HeroSvg vector={vector}/>
                <HomeLeft>
                    <ContainerVideo>
                        <ReactPlayer
                        url={require('../../utils/videos/video.mp4').default}
                        width='80%'
                        height='100%'
                        controls
                        />
                    </ContainerVideo>
                    <Link to="/contact">
                        <ButtomHome className="button" to="/contact">- Contactanos -</ButtomHome>
                    </Link>
                </HomeLeft>
                <HomeRight>
                    <ContainerText>
                    HRF ASOCIADOS se constituy en la ciudad de Bogot D.C., con la unin de capital, trabajo, experiencia y esfuerzos de los socios Hernndez, Rodrguez y Forero, esta sociedad se conform para brindar soluciones en los mbitos contables, financieros y legales, inicialmente a nivel nacional y con la visin de expansin a nivel internacional, buscando alianzas y haciendo aplicacin del trabajo colaborativo.<br/> 
                    <ButtonMobile>
                    <Link to="/services">
                        <ButtomHome className="button" to="/contact">- Servcios -</ButtomHome>
                    </Link>
                    </ButtonMobile>
                    Nuestra compaa es especializada en la prestacin de servicios de manera independiente, responsable e integra, buscando cumplir con las expectativas de nuestros clientes, mediante el asesoramiento de nuestro equipo de trabajo multidisciplinario, el cual tiene enfoque en diferentes reas como; Outsourcing Contable, Revisora Fiscal, Consultora Legal y Tributaria, aseguramiento y, defensa jurdica en temas civiles, laborales, comerciales, administrativos y disciplinarios relacionados con la profesin Contable.
                    </ContainerText>
                </HomeRight>
        </InfoHome>
        </>
    )
}

export default InfoHRFHome
